import store from '../store'

export async function listenGuard(to, from, next){
    await store.dispatch('getDj',to.params.handle)
    if(store.state.dj){
        next()
    }else{
        next({
            name:'NotFound',
        })
    }
}

export async function authGuard(to, from, next){
        await store.dispatch('checkAuth')
        if(store.state.me){
            if(to.meta.onlyStreamer){
                if(store.state.me.streamer){
                    next()
                }else{
                    next({
                        name:'Home',
                    })
                }
            }else{
               next() 
            }            
        }else{
            next({
                name:'Home',
            })
        }
}
