<template>
    <nav class="navbar is-light is-fixed-top" role="navigation" aria-label="main navigation" shadow>
        <div class="navbar-brand">
            <div class="navbar-item" >
            <router-link to="/"><img src="../assets/earphones.png" height="50px"></router-link>
            </div>

            <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" @click="toggleMobile">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            </a>
        </div>

        <div class="navbar-menu" :class="{'is-active':openMobile}">
            <div class="navbar-start">
                <a class="navbar-item">
                    Discover
                </a>

                <a class="navbar-item" v-if="me">
                    Subscriptions
                </a>

                <a class="navbar-item" v-if="me">
                    Following
                </a>
            </div>

            <b-autocomplete
                icon="magnify"
                ref="autocomplete"
                placeholder="Search"
                class="navbar-item">
                
                <template slot="empty">No results found</template>
            </b-autocomplete>

            <div class="navbar-end">
                <div class="navbar-item" v-if="!me">
                    <div class="buttons">
                    <a class="button is-light outlined" @click="openLogin(0)">
                        Log in
                    </a>
                    <a class="button is-primary" @click="openLogin(1)">
                        <strong>Sign up</strong>
                    </a>
                    
                    </div>
                </div>
                <div class="navbar-item has-dropdown is-hoverable" v-if="me">
                    <a class="navbar-link is-arrowless image">
                        <img class="is-rounded" style="max-width:28px" v-if="me.pictures.length  && me.streamer" :src="me.pictures[0]">
                        <img class="is-rounded" style="max-width:28px"  v-else-if="me.picture" :src="me.picture.data.url">
                        <b-skeleton circle height="28px" width="28px" v-else :animated="false"></b-skeleton>
                    </a>

                    <div class="navbar-dropdown is-right">
                        <div class="media pl-3 pb-1 is-hidden-mobile">
                            <div class="media-left image is-48x48 is-marginless">
                                <img class="is-rounded" style="max-height: 48px;"  v-if="me.pictures.length && me.streamer" :src="me.pictures[0]">
                                <img class="is-rounded" style="max-height: 48px;"  v-else-if="me.picture" :src="me.picture.data.url">
                                <b-skeleton circle height="48px" width="48px" v-else :animated="false"></b-skeleton>
                            </div>
                            <div class="media-content pl-3">
                                <span class="has-text-weight-semibold">{{me.name}} {{me.lastname}}</span> <br>
                                <span v-if="me.streamer">@{{me.handle}}</span>
                            </div>
                        </div>
                        <hr class="navbar-divider">
                    <a class="navbar-item" @click="routePush('/profile')">
                        <b-icon icon="account" size="is-small" class="pr-1"></b-icon><span> Profile</span> <span style="display:inline-block; width: 60px;"></span>
                    </a>
                    <a class="navbar-item" @click="routePush('/'+me.handle)" v-if="me.streamer">
                        <b-icon icon="monitor" size="is-small" class="pr-1"></b-icon><span> My Channel</span> 
                    </a>
                    <a class="navbar-item" @click="routePush('/console')" v-if="me.streamer">
                        <b-icon icon="view-dashboard" size="is-small" class="pr-1"></b-icon><span> DJ Console</span> 
                    </a>
                    <!-- <a class="navbar-item">
                        <b-icon icon="cogs" size="is-small" class="pr-1"></b-icon><span> Account Settings</span> 
                    </a> -->
                    <hr class="navbar-divider">
                    <a class="navbar-item" @click="clickLogout">
                        <b-icon icon="exit-to-app" size="is-small"></b-icon><span> Logout</span> 
                    </a>
                    </div>
                </div>
            </div>
        </div>
    </nav>
</template>

<script>
import {mapState,mapActions} from 'vuex'
import LoginModal from '@/components/LoginModal' 
export default {
    data(){
        return {
            openMobile:false
        }
    },
    components:{
        // LoginModal
    },
    methods:{
        toggleMobile(){
            this.openMobile = !this.openMobile
        },
        openLogin(section){
            this.$buefy.modal.open({
                parent: this,
                component: LoginModal,
                hasModalCard: true,
                props:{
                    activeTab:section,
                },
                canCancel:['escape', 'outside'],
                customClass: '',
                trapFocus: true
            })
        },
        clickLogout(){
            this.logout()
            if(this.$route.path !== '/') {
                this.$router.push({ path: '/' })
            }
        },
        routePush(route){
            this.$router.push({ path: route })
        },
        ...mapActions([
            'logout'
        ])
    },
    computed:{
        ...mapState([
            'me'
        ])
    }
}
</script>
