<template>
    <b-tabs position="is-centered" v-model="activeTab">
            <b-tab-item label="Profile">
                
                <p class="title is-3">Profile</p>
                <div class="columns is-5">
                    <div class="column">
                        <p class="subtitle is-5">Profile Picture</p>
                        <div class="box">
                            <div class="media">
                                <div class="media-left">
                                    <div class="image">
                                        <img class="is-rounded" style="max-height: 48px;max-width: 48px;"  v-if="me.picture&&!isUploading" :src="me.picture.data.url">
                                        <b-skeleton circle height="48px" width="48px" v-else :animated="isUploading"></b-skeleton>
                                    </div>
                                </div>
                                <div class="media-content">
                                    <b-upload v-model="pictureUpload" 
                                    drag-drop 
                                    expanded 
                                    accept='image/*' 
                                    @input="uploadProfilePicture" 
                                    :loading="isUploading">
                                        <section class="section">
                                        <div class="content has-text-centered">
                                            <p>
                                                <b-icon icon="upload" size="is-large"></b-icon>
                                            </p>
                                            <p>Drop your picture here or click to upload</p>
                                        </div>
                                        </section>
                                    </b-upload>
                                </div>
                            </div>
                        </div>
                        <p class="subtitle is-5">Email Confirmation</p>
                        <div class="box">
                            <b-field label="Email">
                                <b-input v-model="me.email" placeholder="Please fill you email to validate it" :disabled="me.email"></b-input>
                            </b-field>
                            <b-button type="is-primary" :disabled="me.emailIsVerified">
                                <span v-if="me.emailIsVerified">Email Confirmed</span>
                                <span v-else>Send Confirmation</span>
                            </b-button>    
                        </div>
                        <p class="subtitle is-5">Social Connections</p>
                        <div class="box px-7">
                            <b-button class="button is-facebook" expanded icon-left="facebook" tag="a" href="/auth/facebook" :disabled="me.facebookId">
                                <span v-if="me.facebookId">Connected with Facebook</span>
                                <span v-else>Connect with Facebook</span>
                            </b-button>
                        </div>
                    </div>
                    <div class="column">
                        <p class="subtitle is-5">Personal Information</p>
                        <div class="box">
                            <b-field label="Name">
                                <b-input v-model="me.name" :loading="personalInformationLoading" :disabled="personalInformationLoading" maxlength="15"></b-input>
                            </b-field>
                            <b-field label="Last Name">
                                <b-input v-model="me.lastname" :loading="personalInformationLoading" :disabled="personalInformationLoading" maxlength="15"></b-input>
                            </b-field>
                            <b-button type="is-primary" :loading="personalInformationLoading" @click="updatePersonalInformation" :disabled="!canUpdatePersonalInformation">Update</b-button>
                        </div>
                        <p class="subtitle is-5">Change Password</p>
                        <div class="box">
                            <b-field label="New Password">
                                <b-input required v-model="newPassword" type="password" password-reveal placeholder="••••••••••" :loading="changePasswordLoading" :disabled="changePasswordLoading"></b-input>
                            </b-field>
                            <b-field label="Confirm New Password">
                                <b-input required v-model="confirmNewPassword" type="password" password-reveal placeholder="••••••••••" :loading="changePasswordLoading" :disabled="changePasswordLoading"></b-input>
                            </b-field>
                            <b-button type="is-primary" :loading="changePasswordLoading" :disabled="!canUpdatePassword" @click="updatePassword">Change Password</b-button>
                        </div>
                    </div>
                </div>
                

            </b-tab-item>
        </b-tabs>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import axios from 'axios'
export default {
    data(){
        return {
            activeTab:0,
            pictureUpload:null,
            isUploading:false,
            confirmNewPassword:"",
            newPassword:"",
            personalInformationLoading:false,
            changePasswordLoading:false,
            emailConfirmationLoading:false
        }
    },
    methods:{
        async uploadProfilePicture(){
            this.isUploading=true
            var form = new FormData()
            form.append('profile',this.pictureUpload)
            await axios.put('/api/profilePicture',form)
            await this.checkAuth()
            this.isUploading=false
        },
        async updatePersonalInformation(){
            this.personalInformationLoading=true
            await this.editMe({
                name:this.me.name.trim(),
                lastname:this.me.lastname.trim()
            })
            this.personalInformationLoading=false
        },
        async updatePassword(){
            this.changePasswordLoading=true
            await this.changePassword({
                newSecret:this.newPassword
            })
            this.newPassword=""
            this.confirmNewPassword=""
            this.changePasswordLoading=false
        },
        ...mapActions([
            'editMe',
            'checkAuth',
            'changePassword'
        ])
    },
    computed:{
        canUpdatePersonalInformation(){
            return this.me.name.length && this.me.lastname.length
        },
        canUpdatePassword(){
            return this.newPassword.length >5 && this.newPassword == this.confirmNewPassword
        },
        ...mapState([
            'me'
        ])
    }
}
</script>
