import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VuexPersistence from 'vuex-persist'

Vue.use(Vuex)

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: (state) => (
    {
      me: state.me,
      miniSidebar:state.miniSidebar
    })
})

export default new Vuex.Store({
  state: {
    miniSidebar:false,
    me:null,
    dj:null,
    popular:[],
    featured:null,
    categories:[],
    recommended:[],
    following:[],
    subs:[],
    sideBarLoading:true,
    homeLoading:false,
    fullLoading:false,
  },
  mutations: {
    TOGGLE_MINI_SIDEBAR:(state)=>(state.miniSidebar=!state.miniSidebar),
    SET_ME:(state,me)=>(state.me=me),
    SET_DJ:(state,dj)=>(state.dj = dj),
    SET_FEATURED:(state,featured)=>(state.featured = featured),
    SET_FOLLOWING:(state,following)=>(state.following = following),
    SET_SUBS:(state,subs)=>(state.subs = subs),
    SET_RECOMMENDED:(state,recommended)=>(state.recommended = recommended),
    SET_CATEGORIES:(state,categories)=>(state.categories = categories),
    SET_FULL_LOADING_STATE:(state,value)=>(state.fullLoading=value),
    SET_HOME_LOADING_STATE:(state,value)=>(state.homeLoading=value),
    SET_SIDE_LOADING_STATE:(state,value)=>(state.sideBarLoading=value)
  },
  actions: {
    async getDj({commit},handle){
      commit('SET_FULL_LOADING_STATE',true)
      const res = await axios.get(`/public/getStreamer/${handle}`)
      commit('SET_DJ',res.data)
      commit('SET_FULL_LOADING_STATE',false)
    },
    async checkAuth({commit}){
      const res = await axios.get('/auth/check')
      commit('SET_ME',res.data)
    },
    async login({commit},loginInfo){
      commit('SET_FULL_LOADING_STATE',true)
      const res = await axios.post('auth/login',
      {
        username:loginInfo.user,
        password:loginInfo.pass
      }).catch(()=>{
      })
      if(res){
        commit('SET_ME',res.data)
        commit('SET_FULL_LOADING_STATE',false)
        return true
      }else{
        commit('SET_FULL_LOADING_STATE',false)
        return false
      }
    },
    async logout({commit}){
      commit('SET_FULL_LOADING_STATE',true)
      const res = await axios.get('/auth/logout')
      commit('SET_ME',res.data)
      commit('SET_FULL_LOADING_STATE',false)
    },
    async signUp({commit},signUpInfo){
      commit('SET_FULL_LOADING_STATE',true)
      const res = await axios.post('auth/signup',
      {
        email:signUpInfo.user,
        pass:signUpInfo.pass
      }).catch(()=>{
      })
      if(res){        
        const newres = await axios.post('auth/login',
        {
          username:signUpInfo.user,
          password:signUpInfo.pass
        }).catch(()=>{
        })
        commit('SET_ME',newres.data)
        commit('SET_FULL_LOADING_STATE',false)
        return true
      }else{
        commit('SET_FULL_LOADING_STATE',false)
        return false
      }
      
    },
    async getFeatured({commit}){
      const res = await axios.get('/public/getOneStreamer')
      commit('SET_FEATURED',res.data)
    },
    async getRecommended({commit},qty=5){
      const res = await axios.get('/public/getStreamers/'+qty)
      commit('SET_RECOMMENDED',res.data)
    },
    async getCategories({commit}){
      const res = await axios.get('/public/getCategories')
      commit('SET_CATEGORIES',res.data)
    },
    async editMe({commit},data){
      const res = await axios.patch('/api/user',data).catch(()=>{})
      commit('SET_ME',res.data)
    },
    async changePassword({commit},pass){      
      const res = await axios.patch('/api/changePassword',pass).catch(()=>{})
      commit('SET_ME',res.data)
    }
  },
  plugins: [vuexLocal.plugin]
})
