<template>
    <b-tabs position="is-centered" v-model="activeTab" class="block">
            <b-tab-item label="Channel & Stream Information">
                <p class="title is-3">Channel & Stream Information</p>
                <div class="columns is-5">
                    <div class="column">
                        <p class="subtitle is-5">Channel Profile Picture & Cover</p>
                        <div class="box">
                            <div class="columns">
                                <div class="column">
                                    <div class="media">
                                        <div class="media-left">
                                            <div class="image">
                                                <img class="is-rounded" style="max-height: 64px;max-width: 64px;"  v-if="me.pictures.length&&me.pictures[0]&&!profilePictureUploading" :src="me.pictures[0]">
                                                <b-skeleton circle height="64px" width="64px" v-else :animated="profilePictureUploading"></b-skeleton>
                                            </div>
                                        </div>
                                        <div class="media-content">
                                            <b-upload v-model="pictureUpload" 
                                            drag-drop 
                                            expanded 
                                            accept='image/*' 
                                            @input="uploadChannelPicture" 
                                            :loading="profilePictureUploading">
                                                <section class="section">
                                                <div class="content has-text-centered">
                                                    <p>
                                                        <b-icon icon="upload" size="is-large"></b-icon>
                                                    </p>
                                                    <p>Drop your picture here or click to upload</p>
                                                </div>
                                                </section>
                                            </b-upload>
                                        </div>
                                    </div>
                                </div>
                                <div class="column">
                                    <div class="media">
                                        <div class="media-left">
                                            <div class="image">
                                                <img style="max-height: 64px;max-width: 114px;border-radius: 5px;"  v-if="me.pictures.length&&me.pictures[1]&&!profileCoverUploading" :src="me.pictures[1]">
                                                <b-skeleton style="border-radius: 5px;"  height="64px" width="114px" v-else :animated="profileCoverUploading"></b-skeleton>
                                            </div>
                                        </div>
                                        <div class="media-content">
                                            <b-upload v-model="coverUpload" 
                                            drag-drop 
                                            expanded 
                                            accept='image/*' 
                                            @input="uploadChannelCover" 
                                            :loading="profileCoverUploading">
                                                <section class="section">
                                                <div class="content has-text-centered">
                                                    <p>
                                                        <b-icon icon="upload" size="is-large"></b-icon>
                                                    </p>
                                                    <p>Drop your cover here or click to upload</p>
                                                </div>
                                                </section>
                                            </b-upload>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <p class="subtitle is-5">Channel Information</p>
                        <div class="box">
                            <b-field label="Name">
                                <b-input v-model="me.info.title" :loading="channelInformationLoading"></b-input>
                            </b-field>
                            <b-field
                            :type="handleFieldType"
                            :message="handleFieldMessage">
                            <template slot="label">
                                Handle
                                    <b-tooltip type="is-dark" label="Help text here for explanation">
                                        <b-icon size="is-small" icon="help-circle-outline"></b-icon>
                                    </b-tooltip>
                                </template>
                                <b-input 
                                icon="at" 
                                v-model="me.handle" 
                                :loading="channelInformationLoading||handleLoading"
                                @focus="handleFocus(true)"
                                @blur="handleFocus(false)"
                                @input="handleCheck"
                                >
                                </b-input>
                            </b-field>
                            <b-field label="Short Description">
                                <b-input type="textarea" v-model="me.info.description" :loading="channelInformationLoading"></b-input>
                            </b-field>
                            <b-field label="Biography">
                                <!-- <b-input type="textarea" v-model="me.info.bio" :loading="channelInformationLoading"></b-input> -->
                                <editor
                                    v-model="me.info.bio"
                                    api-key="pttybc5l1xslkzgjt8fiansalp4ells0pp16nrrfy6w39p2n"
                                    :init="{
                                        height: 350,
                                        menubar: false,
                                        plugins: [
                                        'advlist autolink lists link image charmap print preview anchor',
                                        'searchreplace visualblocks code fullscreen',
                                        'insertdatetime media table paste code help wordcount'
                                        ],
                                        toolbar:
                                        'undo redo | formatselect | bold italic backcolor | \
                                        alignleft aligncenter alignright alignjustify | \
                                        bullist numlist outdent indent | image removeformat'
                                    }"
                                />
                            </b-field>
                            <b-button type="is-primary" :loading="channelInformationLoading" @click="updateChannelInformation" :disabled="!canUpdateChannelInformation">Update</b-button>
                        </div>
                        <p class="subtitle is-5">Social Connections</p>
                        <div class="box">
                            <b-field label="Facebook">
                                <b-input icon="facebook" v-model="me.info.social.facebook" :loading="socialConnectionsLoading"></b-input>
                            </b-field>
                            <b-field label="Instagram">
                                <b-input icon="instagram" v-model="me.info.social.instagram" :loading="socialConnectionsLoading"></b-input>
                            </b-field>
                            <b-field label="Twitter">
                                <b-input icon="twitter" v-model="me.info.social.twitter" :loading="socialConnectionsLoading"></b-input>
                            </b-field>
                            <b-field label="Spotify">
                                <b-input icon="spotify" v-model="me.info.social.spotify" :loading="socialConnectionsLoading"></b-input>
                            </b-field>
                            <b-field label="Soundcloud">
                                <b-input icon="soundcloud" v-model="me.info.social.soundcloud" :loading="socialConnectionsLoading"></b-input>
                            </b-field>
                            <b-button type="is-primary" :loading="socialConnectionsLoading" @click="updateSocialConnections">Update</b-button>
                        </div>
                    </div>
                    <div class="column">
                        <p class="subtitle is-5">Stream Information</p>
                        <div class="box">
                            <b-field label="Title">
                                <b-input v-model="me.info.subtitle" :loading="streamInformationLoading"></b-input>
                            </b-field>
                            <b-field label="Categories">
                                <b-taginput
                                    v-model="me.categories"
                                    maxtags="3"
                                    autocomplete
                                    :data="filteredCategories"
                                    @typing="getFilteredCategories"
                                    field="name"
                                     :loading="streamInformationLoading">
                                    <template slot="empty">
                                        There are no categories
                                    </template>
                                </b-taginput>
                            </b-field>
                            <b-button type="is-primary" :loading="streamInformationLoading" @click="updateStreamInfo">Update</b-button>
                        </div>
                        <p class="subtitle is-5">
                        Stream Access Key 
                        <b-tooltip multilined type="is-dark" label="Never share your stream access key with anyone or show it on stream! Our Staff will never ask you for this information.">
                            <b-icon size="is-small" icon="alert-circle-outline"></b-icon>
                        </b-tooltip>
                        </p>
                        <div class="box">
                            <b-field label="Key">
                                <b-input type="password" disabled password-reveal :value="'now_'+me.token"></b-input>
                            </b-field>
                            <b-collapse :open="false" position="is-bottom" aria-id="contentIdForA11y1">
                                <a slot="trigger" slot-scope="props" aria-controls="contentIdForA11y1">
                                    <b-icon :icon="!props.open ? 'menu-down' : 'menu-up'"></b-icon>
                                    {{ !props.open ? 'Show Instructions' : 'Hide Instructions' }}
                                </a>
                                <p>
                                    Go to you OBS Studio Settings <br/>
                                    Navigate to Stream Settings at the menu on the left and fill out the following options: <br/><br/>
                                    Service: Custom... <br/>
                                    Server: rtmp://XXX.XXX.XXX.XXX/beat <br/>
                                    Stream Key: -- insert your secret stream access key --
                                </p>
                            </b-collapse>
                        </div>
                    </div>
                </div>
            </b-tab-item>
            <b-tab-item disabled>
                <template slot="header">
                    <span> Control & Statistics <b-tag > Comming Soon </b-tag> </span>
                </template>
                <div class="columns is-5">
                    <div class="column">
                        
                    </div>
                    <div class="column is-two-thirds">
                        
                    </div>
                    <div class="column">
                        
                    </div>
                </div>
            </b-tab-item>
    </b-tabs>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import _ from 'lodash'
import axios from 'axios'
import Editor from '@tinymce/tinymce-vue'

export default {
    data(){
        return {
            activeTab:0,
            pictureUpload:null,
            profilePictureUploading:false,
            filteredCategories:this.categories,
            channelInformationLoading:false,
            streamInformationLoading:false,
            socialConnectionsLoading:false,
            handleFieldType:"",
            handleFieldMessage:"",
            handleLoading:false,
            handleAvailable:true,
            profileCoverUploading:false,
            coverUpload:null
        }
    },
    components: {
     Editor
    },
    methods:{
        handleCheck: _.debounce(async function(){
            this.handleLoading=true
            const userWithHandle = await axios.get(`/public/getStreamer/${this.me.handle}`)
            this.handleLoading=false            
            if(!userWithHandle.data){
                if(this.me.handle.length<4){
                    this.handleFieldType="is-warning",
                    this.handleFieldMessage="Your handle must me at least 4 characters long."
                    this.handleAvailable=false
                }else{
                    this.handleFieldType="is-success",
                    this.handleFieldMessage="This handle is available."
                    this.handleAvailable=true
                }
            }else if(userWithHandle.data._id == this.me._id){
                this.handleFieldType="",
                this.handleFieldMessage=""
                this.handleAvailable=true
            }else{
                this.handleFieldType="is-danger",
                this.handleFieldMessage="This handle is already in use, please try something else."
                this.handleAvailable=false
            }            
        },500),
        handleFocus(on){
            if(on){
                this.handleFieldType="is-warning",
                this.handleFieldMessage="Remember that your handle is unique and the best way for people to find you, change it only if it's a must."
            }else{
                this.handleFieldType="",
                this.handleFieldMessage=""
            }
            
        },
        async uploadChannelPicture(){
            this.profilePictureUploading=true
            var form = new FormData()
            form.append('channel',this.pictureUpload)
            await axios.put('/api/channelPicture',form)
            await this.checkAuth()
            this.profilePictureUploading=false
        },
        async uploadChannelCover(){
            this.profileCoverUploading=true
            var form = new FormData()
            form.append('cover',this.coverUpload)
            await axios.put('/api/channelCover',form)
            await this.checkAuth()
            this.profileCoverUploading=false
        },
        getFilteredCategories(text) {           
            this.filteredCategories = this.categories.filter((option) => {
                return option.name
                    .toString()
                    .toLowerCase()
                    .indexOf(text.toLowerCase()) >= 0
            })
        },
        async updateChannelInformation(){
            this.channelInformationLoading=true
            await this.editMe({
                handle:this.me.handle,
                info:{
                    title:this.me.info.title.trim(),
                    subtitle:this.me.info.subtitle.trim(),
                    description:this.me.info.description.trim(),
                    bio:this.me.info.bio.trim(),
                    thumbnail:this.me.info.thumbnail.trim(),                    
                    social:{
                        facebook:this.me.info.social.facebook.trim(),
                        instagram:this.me.info.social.instagram.trim(),
                        spotify:this.me.info.social.spotify.trim(),
                        soundcloud:this.me.info.social.soundcloud.trim(),
                        twitter:this.me.info.social.twitter.trim()
                    }                    
                }
            })
            this.channelInformationLoading=false
        },
        async updateStreamInfo(){
            this.streamInformationLoading=true
            await this.editMe({
                info:{
                    title:this.me.info.title.trim(),
                    subtitle:this.me.info.subtitle.trim(),
                    description:this.me.info.description.trim(),
                    bio:this.me.info.bio.trim(),
                    thumbnail:this.me.info.thumbnail.trim(),                    
                    social:{
                        facebook:this.me.info.social.facebook.trim(),
                        instagram:this.me.info.social.instagram.trim(),
                        spotify:this.me.info.social.spotify.trim(),
                        soundcloud:this.me.info.social.soundcloud.trim(),
                        twitter:this.me.info.social.twitter.trim()
                    }                    
                },
                categories: Array.from(this.me.categories,cat=>cat._id)
            })
            this.streamInformationLoading=false
        },
        async updateSocialConnections(){
            this.socialConnectionsLoading=true
            await this.editMe({
                info:{
                    title:this.me.info.title.trim(),
                    subtitle:this.me.info.subtitle.trim(),
                    description:this.me.info.description.trim(),
                    bio:this.me.info.bio.trim(),
                    thumbnail:this.me.info.thumbnail.trim(),                    
                    social:{
                        facebook:this.me.info.social.facebook.trim(),
                        instagram:this.me.info.social.instagram.trim(),
                        spotify:this.me.info.social.spotify.trim(),
                        soundcloud:this.me.info.social.soundcloud.trim(),
                        twitter:this.me.info.social.twitter.trim()
                    }                    
                }
            })
            this.socialConnectionsLoading=false
        },
        ...mapActions([
            'editMe',
            'checkAuth'
        ])
    },
    computed:{
        canUpdateChannelInformation(){
            return this.me.handle.length && this.handleAvailable
        },
        canUpdateStreamInfo(){
            return true
        },
        canUpdateSocialConnections(){
            return true
        },
        ...mapState({
            me:'me',
            categories:'categories'
        })
    }

}
</script>
