<template>
    <b-sidebar
      position="static"
      type="is-white"
      :reduce="miniSidebar"
      class=""
      open
    >
    <div class="px-2 pt-3 is-fullheight">
      <div class="columns is-marginless" v-if="me && subs.length">
        <div class="column" v-show="!miniSidebar">
          <p>My Subscriptions</p>
        </div>
        <div class="column has-text-centered"
        :class="{'is-narrow':!miniSidebar}">
          <b-button
            @click="toggleSidebar"
            class="is-small"
            :icon-left="miniSidebar?'arrow-collapse-right':'arrow-collapse-left'">
          </b-button>
        </div>
      </div>
      <p class="has-text-centered" v-if="me && subs.length">
        <b-icon icon="playlist-check"  v-show="miniSidebar"></b-icon>
      </p>
      
      
      <div class="columns is-marginless"  v-show="!miniSidebar" v-if="me && following.length">
        <div class="column">
          <p>Following</p>          
        </div>
      </div>
      <p class="has-text-centered" v-if="me && following.length">
        <b-icon icon="heart"  v-show="miniSidebar"></b-icon>
      </p>
      
      <div class="columns is-marginless" >
        <div class="column" v-show="!miniSidebar">
          <p>Recommended</p>          
        </div>
        <div v-if="!me || !subs.length" class="column has-text-centered"
        :class="{'is-narrow':!miniSidebar}">
          <b-button
            @click="toggleSidebar"
            class="is-small"
            :icon-left="miniSidebar?'arrow-collapse-right':'arrow-collapse-left'">
          </b-button>
        </div>
      </div>
      <p class="has-text-centered">
        <b-icon icon="text-subject" v-show="miniSidebar"></b-icon>
      </p>
      <side-bar-item v-for="rec in recommended" :key="rec._id" :item="rec" :small="miniSidebar"/>
    </div>
    
      
    </b-sidebar>
</template>

<script>
import { mapState } from 'vuex'
import sideBarItem from '../components/modules/sideBarItem'

export default {
  data(){
    return {
      showMoreRecommended:false,
      showMoreSubs:false,
      showMoreFollowing:false
    }
  },
  components:{
    sideBarItem
  },
  methods:{
    toggleSidebar(){
      this.$store.commit('TOGGLE_MINI_SIDEBAR')
    }
  },
  computed:{
    ...mapState([
      'miniSidebar',
      'me',
      'sideBarLoading',
      'subs',
      'recommended',
      'following'
    ])
  }
}
</script>
