<template>
  <div>
    <!-- <discover-banner/> -->
    <featured-banner></featured-banner>
    <div class="section">      
      <div>
        <div class="has-text-weight-semibold is-size-5 pb-3">
          Most popular
        </div>
        <div class="columns is-multiline" v-if="!recommended.length">
          <div class="column" v-for="i in 5" :key="i*10">
            <mini-stream-item />
          </div>
        </div>
        <div class="columns is-multiline" v-else>
          <div class="column" v-for="rec in recommended.slice(0,recommendedSlice)" :key="rec._id">
            <mini-stream-item :streamer="rec" />
          </div>
        </div>    
      </div>
      <div class="columns is-vcentered" v-show="showMoreRecommendedIcon">
          <div class="column is-hidden-touch">
            <div class="is-divider" ></div>
          </div>
          <div class="column is-narrow">
            <b-button type="is-primary" icon-right="plus-box-outline" inverted @click="getMoreRecommended">Show more</b-button>
          </div>
          <div class="column is-hidden-touch">
            <div class="is-divider" ></div>
          </div>
        </div>
      
      <div>
        <div class="has-text-weight-semibold is-size-5 pb-3 pt-3">
          Categories we think you'll like
        </div>
        <div class="columns is-multiline" v-if="!categories.length"> 
          <div class="column" v-for="i in 9" :key="i" >
              <mini-category-item />
          </div>
        </div>
        <div class="columns is-multiline" v-else>          
          <div class="column" v-for="category in categories.slice(0,categoriesSlice)" :key="category._id">
            <mini-category-item :category="category" />
          </div>
        </div> 
      </div>
      <div class="columns is-vcentered" v-show="!showAllCategories">
        <div class="column is-hidden-touch">
          <div class="is-divider" ></div>
        </div>
        <div class="column is-narrow">
          <b-button type="is-primary" icon-right="plus-box-outline" inverted @click="getAllCategories">Show more</b-button>
        </div>
        <div class="column is-hidden-touch">
          <div class="is-divider" ></div>
        </div>
      </div>      
    </div>
  </div>
