<script>
// import DiscoverBanner from '@/components/DiscoverBanner'
import miniStreamItem from '@/components/modules/miniStreamItem'
import miniCategoryItem from '@/components/modules/miniCategoryItem'
import FeaturedBanner from '@/components/FeaturedBanner'
import { mapState } from 'vuex'
export default {
  data(){
    return {
      showAllCategories:false,
      categoriesSlice:9,
      recommendedSlice:5,
      showMoreRecommendedIcon:true,
    }
  },
  methods:{
    getAllCategories(){
      this.categoriesSlice=this.categories.length
      this.showAllCategories = true
    },
    getMoreRecommended(){
      this.recommendedSlice=this.recommendedSlice+5
      if(this.recommendedSlice>this.recommended.length){
        this.showMoreRecommendedIcon = false
      }
      
    }
  },
  components:{
    // DiscoverBanner,
    miniStreamItem,
    miniCategoryItem,
    FeaturedBanner
  },
  computed:{
    ...mapState([
      'categories',
      'recommended',
      'me'
    ])
  }
}
</script>
