<template>
        <div class="modal-card">
            <section class="modal-card-body">
                <div class="columns">
                    <div class="column"></div>
                    <div class="column is-narrow">
                        <p class="modal-card-title has-text-centered">            
                            <img src="../assets/earphones.png" alt=""  width="60px"><br>
                            Login on DJ Now!
                        </p>
                    </div>
                    <div class="column"></div>
                </div>
                
                <b-tabs v-model="internalActiveTab" >
                    <b-tab-item label="Log In">
                        <b-field label="Email">
                            <b-input
                                type="email"
                                v-model="email"
                                placeholder=""
                                required>
                            </b-input>
                        </b-field>

                        <b-field label="Password">
                            <b-input
                                type="password"
                                v-model="password"
                                password-reveal
                                placeholder=""
                                required
                                @keyup.enter.native="emailLogin">
                            </b-input>
                        </b-field>
                        <b-button class="button is-primary mt-5" :disabled="!canLogin" expanded @click="emailLogin">Log in</b-button>
                        <div class="is-divider" data-content="OR" ></div>
                        <b-button class="button is-facebook mb-2" expanded icon-left="facebook" tag="a" href="/auth/facebook">Log in with Facebook</b-button>
                        <!-- <b-button class="button is-instagram mt-3" disabled expanded icon-left="instagram" tag="a" href="/auth/instagram">Log in with Instagram</b-button> -->
                    </b-tab-item>

                    <b-tab-item label="Sign Up">
                        <b-field label="Email"
                            :message="signUpEmailMessage">
                            <b-input
                                type="email"
                                v-model="newEmail"
                                placeholder=""
                                required>
                            </b-input>
                        </b-field>

                        <b-field label="Password">
                            <b-input
                                type="password"
                                v-model="newPassword"
                                password-reveal
                                placeholder=""
                                required>
                            </b-input>
                        </b-field>
                        <b-field label="Confirm Password">
                            <b-input
                                type="password"
                                v-model="confirmNewPassword"
                                password-reveal
                                placeholder=""
                                required>
                            </b-input>
                        </b-field>
                        <b-button class="button is-primary mt-5" :disabled="!canSignup" expanded @click="emailSignUp">Sign up</b-button>
                        <div class="is-divider" data-content="OR" ></div>
                        <b-button class="button is-facebook mb-2" expanded icon-left="facebook" tag="a" href="/auth/facebook">Sign up with Facebook</b-button>
                        <!-- <b-button class="button is-instagram mt-3" expanded icon-left="instagram" tag="a" to="/auth/instagram" disabled>Sign up with Instagram</b-button> -->
                    </b-tab-item>
                </b-tabs>
                
                
            </section>
        </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
    data(){
        return {
            email:'',
            password:'',
            newEmail:'',
            newPassword:'',
            confirmNewPassword:'',
            internalActiveTab:this.activeTab,
            signUpEmailMessage:'',
            signUpPasswordMessage:'',
            signUpConfirmPasswordMessage:'',
            loginEmailMessage:'',
            loginPasswordMessage:''
        }
    },
    props:{
        activeTab:{
            type: Number,
            default:0
        }
    },
    methods:{
        async emailLogin(){
            if(this.canLogin){
                const res = await this.login({
                    user:this.email,
                    pass:this.password
                })
                if(res){
                    this.$parent.close()
                }else{
                    this.$buefy.dialog.alert({
                        title: 'Alert',
                        message: "We've encounter some issues with your credentials, please check your email and password and try again.",
                        confirmText: 'Ok'
                    })
                    this.password=""
                }
            }
        },
        async emailSignUp(){
            const res = await this.signUp({
                user:this.newEmail,
                pass:this.newPassword
            })
            if(res){
                this.$buefy.dialog.alert({
                    title: 'Success',
                    message: "Welcome! We will log you into your brand new account.",
                    confirmText: 'Go'
                })
                this.$parent.close()
            }else{
                this.$buefy.dialog.alert({
                    title: 'Alert',
                    message: "We've encounter some issues with your credentials, please check your email and try again.",
                    confirmText: 'Ok'
                })
            }
        },
        ...mapMutations([
            'SET_ME'
        ]),
        ...mapActions([
            'login',
            'signUp'
        ])
    },
    computed: {
        ...mapState([
            'me'
        ]),
        canLogin(){
            var re =   /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/
            return re.test(this.email) && this.password.length > 5
        },
        canSignup(){
            var re =   /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/
            return re.test(this.newEmail) && this.newPassword.length > 5 && this.confirmNewPassword.length > 5 && this.newPassword==this.confirmNewPassword
        }
    }
}
</script>
