<template>
  <div id="app">
    <b-loading :active="fullLoading"></b-loading>
    <navigation/>
    <section class="hero is-fullheight-with-navbar">
      <div class="media">
        <div class="media-left is-hidden-touch">
          <side-bar/>
        </div>
        <div class="media-content is-fullheight">
          <router-view/>
        </div>
        
      </div>
      
    </section>
  </div>
</template>

<script>
import Navigation from './components/Navigation'
import SideBar from './components/SideBar'
import {mapState,mapActions} from 'vuex'
export default {
  components:{
    Navigation,
    SideBar
  },
  computed: mapState([
    'fullLoading',
    'categories',
    'recommended'
  ]),
  methods:{
    ...mapActions([
      'checkAuth',
      'getCategories',
      'getRecommended'
    ])
  },
  mounted(){
    this.checkAuth()
    if(!this.categories.length){
      this.getCategories()
    }
    if(!this.recommended.length){
      this.getRecommended()
    }
    
  }
}
</script>