import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import { authGuard, listenGuard } from './guards'
import Profile from '../views/Profile'
import Console from '../views/Console'
import NotFound from '../views/404'
import Settings from '../views/Settings'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/profile',
    name: 'Profile',
    beforeEnter: authGuard,
    component: Profile
  },
  {
    path: '/settings',
    name: 'Settings',
    beforeEnter: authGuard,
    component: Settings
  },
  {
    path: '/console',
    name: 'Console',
    beforeEnter: authGuard,
    meta: { onlyStreamer: true },
    component: Console
  },
  {
    path: '/404',
    name: 'NotFound',
    component: NotFound
  },
  {
    path: '/:handle',
    name: 'Listen',
    beforeEnter: listenGuard,
    component: () => import(/* webpackChunkName: "about" */ '../views/Listen')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
